// libs
import React from "react";
// others
import "./styles.scss";

const NameSingerList = (props) => {
  const temp = props.title.split("-");
  const nameSingerList = temp[1].split(",");
  return (
    <div className="name-singer-list">
      {nameSingerList.map((value) => {
        if (value === nameSingerList[nameSingerList.length - 1])
          return (
            <h4
              className="name-singer"
              title={`Tm cc bi ht, playlist, mv do ca s ${value} trnh by`}
              key={Math.random()}
            >
              {`${value}`}
            </h4>
          );
        return (
          <React.Fragment key={Math.random()}>
            <h4 className="name-singer" title={`Tm cc bi ht, playlist, mv do ca s ${value} trnh by`}>
              {`${value}`}
            </h4>
            <span className="space">,</span>
          </React.Fragment>
        );
      })}
    </div>
  );
};

export default NameSingerList;
